*,
*::after,
*::before {
	box-sizing: border-box;
}

:root {
	font-size: 16px;
}

body {
	--color-text: #fff;
	--color-bg: #0e0e0f;
	--color-link: #826437;
	--color-link-hover: #fff;
	color: var(--color-text);
	background-color: var(--color-bg);
	font-family: moderno-fb, "Big Caslon", "Book Antiqua", "Palatino Linotype", Georgia, serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	-ms-scroll-chaining: none;
	    overscroll-behavior: none;
}

.demo-2 {
	font-family: Futura, "futura-pt", Arial, sans-serif;
}

.demo-2,
.demo-3 {
    --color-link: #fbc966;
}

.demo-4 {
    --color-link: #52e093;
}

#gl {
	width: 100vw;
	height: 100vh;
	left: 0;
	top: 0;
	position: absolute;
}

canvas {
	display: block;
	background-size: cover;
	background-position: 50% 50%;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

/* Page Loader */
.js .loading::before {
	content: '';
	position: fixed;
	z-index: -100000;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: var(--color-bg);
}

.js .loading::after {
	content: '';
	position: fixed;
	z-index: -100000;
	top: 50%;
	left: 50%;
	width: 60px;
	height: 60px;
	margin: -30px 0 0 -30px;
	pointer-events: none;
	border-radius: 50%;
	opacity: 0.4;
	background: var(--color-link);
	-webkit-animation: loaderAnim 0.7s linear infinite alternate forwards;
	        animation: loaderAnim 0.7s linear infinite alternate forwards;
}

@-webkit-keyframes loaderAnim {
	to {
		opacity: 1;
		-webkit-transform: scale3d(0.5,0.5,1);
		        transform: scale3d(0.5,0.5,1);
	}
}

@keyframes loaderAnim {
	to {
		opacity: 1;
		-webkit-transform: scale3d(0.5,0.5,1);
		        transform: scale3d(0.5,0.5,1);
	}
}

a {
	text-decoration: none;
	color: var(--color-link);
	outline: none;
}

a:hover,
a:focus {
	color: var(--color-link-hover);
	outline: none;
}

.message {
	background: var(--color-text);
	color: var(--color-bg);
	padding: 1rem;
	position: fixed;
	top: 0;
	width: 100%;
	z-index: 10000;
	text-align: center;
}

.frame {
	padding: 3rem 5vw;
	text-align: center;
	position: relative;
	z-index: -1000;
	height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.frame > * {
	position: relative;
}

.frame__title {
	font-size: 1.25rem;
	margin: 0;
	font-weight: normal;
}

.frame__tagline {
	display: none;
	margin: 0.5rem 0 2rem;
}

.frame__links {
	margin: 1rem 0;
}

.frame a {
	text-transform: lowercase;
}

.frame__links a:not(:last-child),
.frame__demos a:not(:last-child) {
	margin-right: 1rem;
}

.frame__demos {
	margin: 1rem 0;
}

.frame__demo--current,
.frame__demo--current:hover {
	color: var(--color-text);
}

.frame__content {
	line-height: 0.8;
	pointer-events: none;
	margin-top: 13vh;
	font-size: 40vw;
	font-family: essonnes-display, serif;
	mix-blend-mode: soft-light;
	font-weight: normal;
	text-align: center;
	color: #c2ba8b;
}

.demo-2 .frame__content {
	font-size: 9vw;
	text-transform: uppercase;
	font-weight: bold;
	mix-blend-mode: soft-light;
	text-align: left;
	color: #fff;
	font-family: Futura, "futura-pt", Arial, sans-serif;
}

.demo-3 .frame__content {
	color: #24211d;
}

.demo-4 .frame__content {
	color: #75c298;
	mix-blend-mode: overlay;
}

@media screen and (min-width: 1200px) {
	canvas {
		display: block;
		background-size: cover;
		background-position: 50% 50%;
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		min-height: 687px;
	}
}

@media screen and (min-width: 1600px) {
	.message {
		display: none;
	}

	.background {
		background-size: cover;
		background-position: 50% 50%;
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 0;
	}

	canvas {
		display: block;
		background-size: cover;
		background-position: 50% 50%;
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		min-height: 936px;
	}

	.frame {
		position: fixed;
		text-align: left;
		z-index: -10000;
		top: 0;
		left: 0;
		display: grid;
		align-content: space-between;
		width: 100%;
		max-width: none;
		height: 100vh;
		padding: 2rem 3rem;
		pointer-events: none;
		grid-template-columns: 75% 25%;
		grid-template-rows: auto auto auto;
		grid-template-areas: 'title links'
							'content content'
							'github demos';
	}
	.frame__title-wrap {
		grid-area: title;
		display: flex;
	}
	.frame__title {
		font-size: 1rem;
		margin: 0;
	}
	.frame__tagline {
		margin: 0 0 0 1rem;
		padding: 0 0 0 1rem;
		opacity: 0.6;
		display: block;
	}
	.frame__github {
		grid-area: github;
		justify-self: start;
		margin: 0;
	}
	.frame__demos {
		margin: 0;
		grid-area: demos;
		justify-self: end;
	}
	.frame__links {
		grid-area: links;
		padding: 0;
		justify-self: end;
	}
	.frame__content {
		grid-area: content;
		margin: 0;
	}
	.frame a {
		pointer-events: auto;
	}
}

