$main: #000;
$white: #ffffff;
$black: #1e1f13;

* {
  box-sizing: border-box;
  font-family: "BlairITC TT";
  src: local("BlairITC TT"),
   url("./fonts/blairitc-tt-bold.ttf") format("truetype");
  font-weight: bold;
}

.font-face-gm {
  font-family: "BlairITC TT";
 }
 

body {
  padding: 0;
  margin: 0;
  background-color: $white;
  color: $black;
  // &:before {
  //   content: "";
  //   height: 300%;
  //   left: -50%;
  //   opacity: 1.3;
  //   position: fixed;
  //   top: -100%;
  //   width: 300%;
  // }
  &.no-scroll {
    overflow-y: hidden;
  }
}

//Header

.container {
  flex-grow: 1;
  margin: 0 auto;
  // padding: 0 32px;
  position: relative;
  width: auto;
  height: 100%;
  z-index: -10000000;
  &.fluid {
    width: 100%;
    max-width: 100%;
    padding: 0;
  }
  // @media (min-width: 1024px) {
  //   max-width: 960px;
  // }
  // @media (min-width: 1250px) {
  //   max-width: 1300px;
  // }
  // @media (min-width: 1408px) {
  //   max-width: 1680px;
  // }

  .row {
    display: flex;
    align-items: center;
    &.space-between {
      justify-content: space-between;
    }
    &.center {
      justify-content: center;
    }
  }
}

header {
  font-size: 16px;
  position: fixed;
  z-index: 99;
  width: 100%;
  font-weight: "700";
  .container {
    .row {
      height: 128px;
      .logo {
        a {
          color: $black;
          text-decoration: none;
        }
      }
      .menu {
        cursor: pointer;
        border-radius: 100%;
        border: 1px solid $black;
        height: 80px;
        width: 80px;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: 0.3s ease-in-out;
        &:hover {
          color: $white;
          background: $black;
        }
      }
    }
  }
}

main {
  .container {
    position: relative;
    .row {
      height: 100vh;
      align-items: center;
      .image-container {
        // position: relative;
        width: 150%;
        .thumbnail {
          overflow: hidden;
          position: relative;
          .frame {
            img {
              width: 25%;
              margin: 12% 37% 13% 37%;
            }
          }
        }

        // width: 1843px;
        // height: 931px;

        .information {
          position: absolute;
          width: 100%;
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin-top: 20px;
          text-transform: uppercase;
          .location {
            span:nth-child(2) {
              margin-left: 8px;
            }
          }
        }
      }
    }
  }
}

//single
.single {
  .container {
    .top-row {
      height: 50vh;
      width: 100%;
      align-items: flex-end;
      .top {
        padding-bottom: 40px;
        .details {
          display: flex;
          align-items: center;
          justify-content: space-between;
          .location {
            span:nth-child(2) {
              margin-left: 16px;
            }
          }
          .mua {
            text-transform: uppercase;
          }
        }
        .model {
          overflow: hidden;
          .first {
            margin-right: 72px;
          }
          span {
            display: inline-block;
            position: relative;
            font-weight: 400;
            font-size: 224px;
            font-family: "Maragsa";
            @media (max-width: 1440px) {
              font-size: 128px;
            }
          }
        }
      }
    }
    .bottom-row {
      height: 50vh;
      position: relative;
      .bottom {
        height: 100%;
        width: 100%;
        .thumbnail-single {
          width: 100%;
          height: 800px;
          margin: 0 auto;
          overflow: hidden;
          position: absolute;
          left: 0;
          right: 0;
          .frame-single {
            img {
              position: absolute;
              width: 100%;
            }
          }
        }
      }
      .scroll-for-more {
        position: absolute;
        bottom: 200px;
        left: 200px;
        z-index: 20;
        .icon {
          svg {
            height: auto;
            width: 28px;
          }
        }
        .text {
          margin-top: 8px;
          color: $white;
          text-transform: uppercase;
        }
      }
    }
  }
}

.detailed-information {
  margin-top: 200px;
  height: 600px;
  .container {
    .row {
      justify-content: space-between;
      align-items: flex-start;
      h2 {
        font-size: 28px;
      }
      p {
        font-family: "Helvetica Neue";
        font-size: 16;
        line-height: 28px;
        font-weight: 400;
        width: 800px;
      }
    }
  }
}
